$primary-cyan-main: #4eb7a8;
$primary-cyan-dark: #46ab9d;

$primary-orange-red-main: #e75735;
$primary-orange-red-dark: #d54c2c;

$secondary-gray-main: #888;
$secondary-gray-light: #e5e5e5;
$secondary-gray-dark: #4f4f4f;

$secondary-white-main: #fff;
$secondary-white-smoke: #f2f2f2;
$secondary-white-rose: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff2'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.woff');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

.button {
  border-radius: 0.25rem;

  &_size {
    &_small {
      width: 4.875rem;
    }

    &_standard {
      width: 7.375rem;
    }
  }

  &_width {
    &_hundred-percents {
      width: 100%;
    }
  }

  &_color {
    &_cyan {
      background-color: $primary-cyan-dark;

      &:hover {
        background-color: $primary-cyan-dark;
      }
    }

    &_orange-red {
      background-color: $primary-orange-red-dark;

      &:hover {
        background-color: $primary-orange-red-dark;
      }
    }
  }

  &__button-element {
    border-radius: 0.25rem;
    cursor: pointer;
    font-size: 0.718rem;
    font-weight: 900;
    letter-spacing: 0.01rem;
    outline: 0;
    overflow: hidden;
    padding-top: 0.1rem;
    text-transform: uppercase;
    transform: translate(0, -9%);
    transition: background 0.5s;
    user-select: none;

    &:active {
      transform: translate(0, 0);
    }

    &_size {
      &_small {
        font-size: 0.6rem;
        height: 1.375rem;
        width: 100%;
      }

      &_standard {
        height: 2rem;
        width: 100%;
      }
    }

    &_color {
      &_cyan {
        background-color: $secondary-white-main;
        border: 0.0625rem solid $primary-cyan-main;
        color: $primary-cyan-main;

        &:hover {
          background-color: $primary-cyan-main;
          color: $secondary-white-main;
        }
      }

      &_cyan-inverse {
        background-color: $primary-cyan-main;
        border: 0.0625rem solid $primary-cyan-main;
        color: $secondary-white-main;

        &:hover {
          background-color: $secondary-white-main;
          color: $primary-cyan-main;
        }
      }

      &_orange-red {
        background-color: $secondary-white-main;
        border: 0.0625rem solid $primary-orange-red-main;
        color: $primary-orange-red-main;

        &:hover {
          background-color: $primary-orange-red-main;
          color: $secondary-white-main;
        }
      }

      &_orange-red-inverse {
        background-color: $primary-orange-red-main;
        border: 0.0625rem solid $primary-orange-red-main;
        color: $secondary-white-main;

        &:hover {
          background-color: $secondary-white-main;
          color: $primary-orange-red-main;
        }
      }
    }
  }
}
