$primary-cyan-main: #4eb7a8;
$primary-cyan-dark: #46ab9d;

$primary-orange-red-main: #e75735;
$primary-orange-red-dark: #d54c2c;

$secondary-gray-main: #888;
$secondary-gray-light: #e5e5e5;
$secondary-gray-dark: #4f4f4f;

$secondary-white-main: #fff;
$secondary-white-smoke: #f2f2f2;
$secondary-white-rose: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff2'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.woff');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

.ui-kit-demo {
  &__header {
    margin-top: 1rem;
  }

  &__components-container {
    align-items: center;
    display: flex;
    flex-direction: column;
    margin: 8.8rem auto 6rem;
    width: 35.5%;

    @media screen and (max-width: 1600px) {
      width: 40%;
    }

    @media screen and (max-width: 1366px) {
      width: 50%;
    }

    @media screen and (max-width: 1094px) {
      width: 70%;
    }

    @media screen and (max-width: 722px) {
      width: 90%;
    }
  }

  &__buttons {
    width: 72%;
  }

  &__arrows {
    margin-top: 2.1rem;
    width: 67%;

    @media screen and (max-width: 1094px) {
      margin-top: 2.3rem;
    }
  }

  &__charts {
    margin-top: 2.9rem;
  }

  &__sliders {
    margin-right: 0.93rem;
    margin-top: 1.15rem;
    width: 88%;
  }

  &__stages-box {
    margin-top: 2.3rem;
    width: 84%;
  }

  &__form-elements {
    margin-top: 2.7rem;
    width: 87%;
  }

  &__search-dropdown-profiles {
    margin-top: 1.4rem;
    width: 97%;
  }

  &__news-events {
    margin-top: 5.8rem;
    width: 90%;

    @media screen and (max-width: 1094px) {
      width: 105%;
    }

    @media screen and (max-width: 400px) {
      width: 95%;
    }
  }

  &__location {
    margin-top: 3rem;
    width: 87%;
  }

  &__calendar-messaging {
    margin-top: 0.45rem;
    width: 91%;
  }

  &__video-box {
    margin-top: 1.5rem;
    margin-right: 0.2rem;
    width: 87%;
  }
}
