$primary-cyan-main: #4eb7a8;
$primary-cyan-dark: #46ab9d;

$primary-orange-red-main: #e75735;
$primary-orange-red-dark: #d54c2c;

$secondary-gray-main: #888;
$secondary-gray-light: #e5e5e5;
$secondary-gray-dark: #4f4f4f;

$secondary-white-main: #fff;
$secondary-white-smoke: #f2f2f2;
$secondary-white-rose: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff2'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.woff');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

.text-field {
  background-color: $secondary-gray-light;
  border: 0;
  border-radius: 0.25rem;
  color: $secondary-gray-main;
  font-family: 'Source Sans Pro', Arial, sans-serif;
  font-size: 0.875rem;
  height: 1.75rem;
  letter-spacing: 0.00875rem;
  outline: none;
  padding-left: 0.75rem;
  padding-right: 0.75rem;
  transition: box-shadow 0.3s;

  &:focus {
    box-shadow: 0 0 0.3125rem #000;
  }

  &_type {
    &_for-search-bar {
      border-radius: 0.25rem 0 0 0.25rem;
      box-sizing: border-box;
      font-size: 0.9rem;
      padding: 0 0 0.125rem 0.8rem;
      width: 100%;
    }

    &_for-message-form {
      @at-root {
        input#{&} {
          font-size: 0.9375rem;
          height: 1.5rem;
          padding-bottom: 0.2125rem;
          padding-left: 0.8125rem;
          width: 64%;
        }

        textarea#{&} {
          height: 4.75rem;
          padding-left: 0.84375rem;
          padding-top: 0.50625rem;
          resize: none;
          width: 91%;
        }
      }
    }

    &_for-message-box {
      border: 0.1875rem solid $secondary-white-main;
      height: 5.5rem;
      padding-left: 1.125rem;
      padding-top: 1rem;
      padding-bottom: 1rem;
      resize: none;
      width: 100%;
      box-sizing: border-box;
    }
  }

  &_theme {
    &_red {
      background-color: $primary-orange-red-main;
      color: $secondary-white-main;

      &::placeholder {
        color: $secondary-white-main;
      }
    }
  }
}
