$primary-cyan-main: #4eb7a8;
$primary-cyan-dark: #46ab9d;

$primary-orange-red-main: #e75735;
$primary-orange-red-dark: #d54c2c;

$secondary-gray-main: #888;
$secondary-gray-light: #e5e5e5;
$secondary-gray-dark: #4f4f4f;

$secondary-white-main: #fff;
$secondary-white-smoke: #f2f2f2;
$secondary-white-rose: #fdf4f2;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $font == 'lato' {
    @if $type == 'black' {
      $weight: 900;
    }
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff2'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.woff');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

.block-sign {
  text-align: center;

  &__name {
    color: $secondary-gray-dark;
    display: inline-block;
    font-size: 1.1rem;
    font-weight: 900;
    letter-spacing: 0.09rem;
    text-transform: uppercase;
  
    &_type {
      &_for-charts {
        letter-spacing: 0.065rem;
      }
  
      &_for-search-dropdown {
        letter-spacing: 0.06rem;
        margin-right: 0.8rem;
      }
  
      &_for-profile {
        letter-spacing: 0.08rem;
        margin-right: 1.1rem;
      }
  
      &_for-sliders {
        left: 0.5625rem;
        position: relative;
      }
  
      &_for-calendar {
        letter-spacing: 0.07rem;
        margin-right: 0.35rem;
      }
  
      &_for-messaging {
        margin-right: 0.8rem;
        letter-spacing: 0.07rem;
      }
  
      &_for-video-box {
        margin-right: 0.125rem;
      }
    }
  }
}
